.banana-menu-holder{
  position:fixed;
  width:100%;
  z-index: 200000;
}

header{
  display:table;
  width:100%;
  background:#f5fe29;
  color:#08040c;
  overflow: hidden;
  font-size:40px;
  padding-left:50px;
  padding-right:15px;
  padding-top:2px;
  padding-bottom:2px;
  font-size:34px;
  transition:all 0.3s  linear;
}

header a, header a:hover{
  color:black;
}
.banana-mobile{
  display:none;
}
.banana-burguer-menu{
  float:right;
  font-size:54px;
  font-weight: bold;
  height:50px;
  line-height:0.7;
  /* transform:translatex(25px); */
  position:absolute;
  right:-5px;
}
.banana-mobile-items-holder  {
  height: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.banana-mobile-menu-row {
  width: auto;
  margin-right:10px;
}
.banana-mobile-menu-row div {
  padding: 5px;
  width: 100%;
  margin: 10px;
  line-height: 20px;
  font-size: 1em;
  text-align: center;
  float:right;
  margin-right:0;
}
.banana-discord-menu.banana-mobile,.banana-discord-menu.banana-mobile:hover{
  transform:scale(1);
}
.banana-twitter-menu.banana-mobile,.banana-twitter-menu.banana-mobile:hover{
  transform:scale(1) ;
}
@media (max-width: 650px) {
  /* CSS that should be displayed if width is equal to or less than 650px goes here */
  header{
    padding-left:22px;
    padding-top:5px;
  }
  .banana-opensea{
    height:75px !important;
    font-size:28px!important;
  }
  .banana-background{
    margin-top:25px !important;
  }
  .header-logo{
    font-size:38px;
  }
  .banana-desktop{
    display:none;
  }
  .banana-mobile{
    display:table;
  }
  a.banana-anchor {
    display: block;
    position: relative;
    top: -50px;
    visibility: hidden;
  }
}

